/* Resim yükleme */
form{
position: relative;
margin:  auto;
  width: 80%;
  height: 120px;
  border: 4px dashed #fff;
  
}

form input{  
  position: absolute;
width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  outline: none;
  opacity: 0;
  top: 0;
  left: 0;
}
form p{
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  line-height: 170px;
  color: #ffffff;
  font-family: Arial;
}
form button:hover{
  background: #149174;
	color: #0C5645;
}
form button:active{
  border:0;
}

.img-spotify{
  height: 50%;
  width: cal(height);
  overflow: hidden;
  border-radius: 30px;
}


