@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;500&display=swap');
*{
  padding: 0;
  margin: 0 ;
  box-sizing: border-box;
}
body{
  font-family: 'Montserrat', sans-serif;
   background:linear-gradient(to bottom, #B2B0B1);

  
}
.Content{
  display: flex;
  flex-direction: row;
  justify-content:space-evenly;
  text-align: center;
  margin: 30px 0;


}
.App {
  text-align: center;
}
@media (max-width:900px) {
  .Content{
    display: flex;
    
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    

  }
 
  
} @media (max-width:500px) {
.info{
  margin-bottom: 15px;
}
  .Content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
overflow: hidden;
margin: 10px;
box-sizing: border-box;
  }}
.btn{
display: flex;
justify-content: center;
}
  .btn-shopier{
    background-color:#E35F21 ;
    color: white;
    padding: 20px 50px;
    font-weight: 900;
    font-size: 20px;
    border-radius: 15px;
  }
.icon{
  color: white;
}


/* Resim yükleme */
form{
position: relative;
margin:  auto;
  width: 80%;
  height: 120px;
  border: 4px dashed #fff;
  
}

form input{  
  position: absolute;
width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  outline: none;
  opacity: 0;
  top: 0;
  left: 0;
}
form p{
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  line-height: 170px;
  color: #ffffff;
  font-family: Arial;
}
form button:hover{
  background: #149174;
	color: #0C5645;
}
form button:active{
  border:0;
}

.img-spotify{
  height: 50%;
  width: cal(height);
  overflow: hidden;
  border-radius: 30px;
}

